class App extends React.Component {
  videoRef = React.createRef();
  canvasRef = React.createRef();




  componentDidMount() {
    

    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      const webCamPromise = navigator.mediaDevices
        .getUserMedia({
          audio: false,
          video: {
           // facingMode: "user",
          },
        })
        .then((stream) => {
          window.stream = stream;
          this.videoRef.current.srcObject = stream;
          return new Promise((resolve, reject) => {
            this.videoRef.current.onloadedmetadata = () => {
              resolve();
            };
          });
        });
      const modelPromise = cocoSsd.load();
      Promise.all([modelPromise, webCamPromise])
        .then((values) => {
          this.detectFrame(this.videoRef.current, values[0]);
        })
        .catch((error) => {
          console.error(error);
        });
    }
  }

  detectFrame = (video, model) => {
    model.detect(video).then((predictions) => {
      this.renderPredictions(predictions);
      requestAnimationFrame(() => {
        this.detectFrame(video, model);
      });
    });
  };

  renderPredictions = (predictions) => {
    const ctx = this.canvasRef.current.getContext("2d");
    ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
    // Font options.
    const font = "30px iransans";
    ctx.font = font;
    ctx.textBaseline = "top";
    //console.log(predictions);
    document.getElementById("infobox1").innerText = Object.keys(predictions).length;


    document.getElementById("infobox2").innerText = "";
    document.getElementById("personlogo").style = 'background: none;';
    document.getElementById("car").style = 'background: none;';
    document.getElementById("stoplogo").style = 'background: none;';
    document.getElementById("trafficlightlogo").style = 'background: none;';


    predictions.forEach((prediction) => {
      let tempi = document.getElementById("infobox2").innerText;
     if(prediction.class == "person"){
       document.getElementById("personlogo").style = 'background: green;';
     }
     if(prediction.class == "car"){
      document.getElementById("car").style = 'background: green;';
    }
    if(prediction.class == "stop sign"){
      document.getElementById("stoplogo").style = 'background: green;';
    }
    if(prediction.class == "traffic light"){
      document.getElementById("trafficlightlogo").style = 'background: green;';
    }

      document.getElementById("infobox2").innerText =
        tempi + " " + prediction.class;

      const x = prediction.bbox[0];
      const y = prediction.bbox[1];
      const width = prediction.bbox[2];
      const height = prediction.bbox[3];
      // Draw the bounding box.
      ctx.strokeStyle = "#14213d";
      ctx.lineWidth = 10;
      ctx.strokeRect(x, y, width, height);
      // Draw the label background.
      ctx.fillStyle = "#fca311";
      const textWidth = ctx.measureText(prediction.class).width;
      const textHeight = parseInt(font, 10); // base 10
      ctx.fillRect(x, y, textWidth + 4, textHeight + 4);
    });

    predictions.forEach((prediction) => {
      const x = prediction.bbox[0];
      const y = prediction.bbox[1];
      // Draw the text last to ensure it's on top.
      ctx.fillStyle = "#e5e5e5";
      ctx.fillText(ococofarsi[prediction.class], x, y);
    });
  };

  render() {
    return (
      <div className="container">
        <div id="header" className="row">
          <div id="headin">
            <img src={logocar} alt="Logo" />
            <h1>Smart Ease</h1>
          </div>
        </div>
        <div className="row">
          <div class="col xl6">
            {" "}
            <video
              //className="size"
              autoPlay
              playsInline
              muted
              ref={this.videoRef}
              width="600"
              height="500"
            />
            <canvas
              className="canva"
              ref={this.canvasRef}
              width="600"
              height="500"
            />
          </div>

          <div class="col xl6">
            <div className="row">
              <div className="col s6">
                <div class="row">
                  <div class="col s12">
                    <div class="card blue-grey darken-1">
                      <div class="card-content white-text">
                        <span class="card-title">Objects detected</span>
                        <p style={{ fontSize: "4em" }} id="infobox1">
                          0
                        </p>
                      </div>
                    </div>
                  </div>
                </div>

                <div class="row">
                  <div class="col s12">
                    <div class="card blue-grey darken-1">
                      <div class="card-content white-text">
                        <span class="card-title">Object descriptions</span>
                        <p id="infobox2"> </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col s6">
                
                <div id="personlogo" className="logobox"><img src={person} alt="Logo" /></div>
                <div id="car" className="logobox"><img src={car} alt="Logo" /></div>
                <div id="stoplogo" className="logobox"><img src={stop} alt="Logo" /></div>
                <div id="trafficlightlogo" className="logobox"><img src={trafficlight} alt="Logo" /></div>
                
                </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
